
.container { 
  /* position: relative; */
  width: 70%;
  min-height: 550px;
  overflow: hidden;
  margin-top:50px;
  color: black;
  background: #ffffff;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  border-width: 1px;
  border-color: #a4a4a4;
  /* text-align: center; */

}

.containerForm {
  width: 100%;
  display: flex;
}

.cols {
  width: 50%;
  display: flex;
  flex-direction: column;
}
.buttonContainer{
  margin-bottom: 5%;
}
.button{
  border-radius: 5px;
  padding: 6px;
  border: 1px solid #a4a4a4;
}
.button:hover{
  background-color: #9b9b9b;
  font-weight: 600;
}
.imgContainer{
  text-align: center;
  padding: 5px;
  margin-bottom: 35px;
  width: 100%;
  margin: auto;
}
.playerName{
  text-align: center;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  background-color: #04AA6D;
  color: #fff;
  padding: 8px;
  border-radius: 5px ;   
}
.player{
  width: 35%;
  border: 1px solid #696969;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}
.items{
  list-style: none;
  width: 850px;
  display: flex;
  justify-content: space-between;
  padding: 0;
  
}
.item{
  width: 250px;
  margin-bottom: 10px;
  padding: 10px;
}
.titleHc, .titleFt{
  text-align: center;
  margin-bottom: 30px;
  padding: 15px;
  background-color: #04AA6D;
  color: #fff;
  

}
.containerFt{
  padding: 2rem 0;
}
.titleFe{
  text-align: center;
  margin-bottom: 70px;
}
.containerForm{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  /* justify-content: center; */
  /* padding: 5%; */
  border: none;
}
.rows{
  display: flex;
  width: 100%;
  /* height: 130px; */
  justify-content: space-between;
}
.rowsT{
  display: flex;
  width: 100%;
  /* justify-content: space-between; */
}
.field{
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 0 20px;
  /* padding: 1%; */
}

.field label{
  margin-bottom: 5px;
  font-weight: 500;
} 

.field input{
  height: 30px;
  padding-left: 5px;
  border: 1px solid #696969;
  border-radius: 5px;
}
.field select{
  height: 30px;
  border-radius: 5px;
  border: 1px solid #696969;
}
.textBox{
  display: flex;
  flex-direction: column;
  width: 50%; 
  margin: 0 20px;
}
.textBox label{
  text-align: center;
  font-weight: 500;
}
textarea{
  max-width: 100%;
  max-height: 120px;
  font-size: 15px;
  padding: 5px;
  border: 1px solid #696969;
  border-radius: 5px;
}
.radio{
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  width: 25%;
  /* margin-bottom: 40px ; */
  margin: 0 20px;
  text-align: center;
}
.radio label{
  font-weight: 500;
  margin-bottom: 5px;
}
.radio div{
  display: flex;
  /* justify-content: flex-start; */
  padding-left: 5px;
}

